* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    font-family: sans-serif;
    background-color: #100720;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
main {
    background-color: aliceblue;
    width: 450px;
    height: 500px;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
header h1 {
    text-align: center;
}
header h1::after {
    content: "";
    display: flex;
    margin: auto;
    width: 100px;
    height: 5px;
    margin-top: 10px;
    background-color: cornflowerblue;
}
img {
    max-width: 100%;
}
section {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.img-container {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 50%;
}
.user-img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    position: relative;
}
.img-container::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: cornflowerblue;
    position: absolute;
    top: 0;
    right: -10px;
    border-radius: 50%;
}
.quote-icon {
    position: absolute;
    top: 0;
    background-color: cornflowerblue;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: whitesmoke;
    font-size: 13px;
}
.user-name {
    margin-top: 15px;
    font-size: 25px;
}
.user-job {
    margin-top: 5px;
    font-size: 18px;
    color: #0988d7;
}
.user-text {
    margin-top: 10px;
    height: 100px;
    padding: 0 20px;
    text-align: center;
}
.btn-container {
    margin-top: 10px;
}
.btn {
    background-color: transparent;
    border: none;
    font-size: 30px;
    margin: 0 5px;
    color: rgb(79, 95, 123);
    cursor: pointer;
    transition: color 0.3s;
}
.btn:hover {
    color: #0988d7;
}
.rand-btn {
    border: none;
    background-color: rgba(100, 148, 237, 0.353);
    padding: 5px 10px;
    border-radius: 3px;
    margin-top: 5px;
    color: rgb(0, 92, 168);
    font-weight: bold;
    user-select: none;
    cursor: pointer;
    transition: 0.1s;
}
.rand-btn:hover {
    border: 2px solid rgb(0, 92, 168);
}
